<template>
  <v-container>
    <h1 class="text-center">Registrate</h1>
    <v-spacer></v-spacer>
    <v-form @submit.prevent="agregar_usuario()">
      <v-text-field
        v-model="atributos.nombre"
        :counter="30"
        label="Nombre"
      ></v-text-field>

      <v-text-field
        v-model="atributos.cedula"
        :counter="30"
        label="Cedula"
      ></v-text-field>

      <v-text-field
        v-model="atributos.email"
        label="E-mail"
      ></v-text-field>

      <v-text-field
        v-model="atributos.telefono"
        label="Telefono"
      ></v-text-field>

      <v-text-field
        v-model="atributos.contraseña"
        label="Contraseña"
      ></v-text-field>

      <v-select
        v-model="atributos.sexo"
        :items="items"
        label="Sexo"
      ></v-select>

      <v-dialog
        v-model="dialog"
        persistent
        max-width="290"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            class="mr-4"
            type="sumib"
            v-bind="attrs"
            v-on="on"
          >
            Confirmar
          </v-btn>
        </template>

        <v-card>
          <v-card-title class="text-h5">{{dialog_title}}</v-card-title>
          <v-card-text>{{dialog_text}}</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="green darken-1"
              text
              @click="dialog = false"
            >
              Cerrar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-btn @click="clear()"
        class="mr-4">
        Limpiar
      </v-btn>
      <v-btn @click="inicio()"
        class="mr-4">
        Cancelar
      </v-btn>
    </v-form>
  </v-container>
</template>

<script>
  export default {
    
    data(){
      return {
        Atributos:[],
        
        atributos:
        {
          nombre: '',
          cedula: '',
          telefono: '',
          email: '',
          sexo: '',
          contraseña: '',
        },

        items: ['Masculino','Femenino'],
        
        dialog_title: '',
        dialog_text: '',

        dialog: false,
      }
    },


    methods: {       
      agregar_usuario(){
        console.log(this.atributos)
        this.axios.post('/nuevo-registro', this.atributos)
        .then((res)=>
        {
          this.dialog_title = 'EXITO';
          this.dialog_text = 'Registro exitoso';
          this.Atributos.push(res.data);
        })

        .catch((e)=>{
          this.dialog_title = 'ERROR';
          this.dialog_text = 'Faltan Datos a Ingresar';
          console.log(e.response);
        })

      },

      clear () {
        this.atributos.nombre = '';
        this.atributos.cedula = '';
        this.atributos.telefono = '';
        this.atributos.email = '';
        this.atributos.sexo = '';
        this.atributos.contraseña = '';
        this.atributos.verificarContraseña = '';
        this.atributos.$refs.observer.reset();
      },

      inicio()
      {
        this.$router.push('./')
      }
    },
  }
</script>