<template>
    <v-container>
        <h1 class="text-center">Logueate</h1>
        <v-spacer></v-spacer>

        <v-text-field
          v-model="atributos.email"
          label="E-mail"
          required
        ></v-text-field>

        <v-text-field
          v-model="atributos.contraseña"
          :error-messages="errors"
          label="Contraseña"
          required
        ></v-text-field>  

        <v-btn
          @click="submit()"
          class="mr-4"
          type="submit"
          :disabled="invalid"
        >
          Confirmar
        </v-btn>

        <v-btn @click="inicio" class="mr-4">
          Cancelar
        </v-btn>

    </v-container>
</template>

<script>
  export default {
    
    data(){
      return {
        Atributos:[],
        
        atributos:
        {
          email: '',
          contraseña: '',
        },

        items: ['Masculino','Femenino'],
      }
    },

    methods:
    {      
      submit () {
        this.$refs.observer.validate()
      },

      inicio()
      {
        this.$router.push('./')
      }
    },
  }
</script>