<template>
  <v-container>
    <h1 class="text-center">Crea Evento</h1>
    <v-spacer></v-spacer>
    <v-form @submit.prevent="agregar_evento()">
      <v-text-field
        v-model="atributos.nombre"
        :counter="30"
        label="Nombre Evento"
      ></v-text-field>

      <v-text-field
        v-model="atributos.departamento"
        label="Departamento"
      ></v-text-field>

      <v-text-field
        v-model="atributos.municipio"
        label="Municipio"
      ></v-text-field>

      <v-text-field
        v-model="atributos.fecha"
        label="Fecha"
      ></v-text-field>
      
      <v-text-field
        v-model="atributos.hora"
        label="Hora"
      ></v-text-field>
      
      <v-text-field
        v-model="atributos.descripcion"
        label="Descripcion"
      ></v-text-field>

      <v-dialog
        v-model="dialog"
        persistent
        max-width="290"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            class="mr-4"
            type="sumib"
            v-bind="attrs"
            v-on="on"
          >
            Confirmar
          </v-btn>
        </template>

        <v-card>
          <v-card-title class="text-h5">{{dialog_title}}</v-card-title>
          <v-card-text>{{dialog_text}}</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="green darken-1"
              text
              @click="dialog = false"
            >
              Cerrar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-btn @click="clear()"
        class="mr-4">
        Limpiar
      </v-btn>
      <v-btn @click="inicio()"
        class="mr-4">
        Cancelar
      </v-btn>
    </v-form>
  </v-container>
</template>

<script>
  export default {
    
    data(){
      return {
        Atributos:[],

        atributos:
        {
          nombre: '',
          departamento: '',
          municipio: '',
          fecha: '',
          hora: '',
          descripcion: '',
        },

        dialog_title: '',
        dialog_text: '',
        dialog: false,
      }
    },


    methods: { 
      
      agregar_evento(){
        console.log(this.atributos)
        this.axios.post('/nuevo-evento', this.atributos)
        .then((res)=>
        {
          this.dialog_title = 'EXITO';
          this.dialog_text = 'Registro exitoso';
          this.Atributos.push(res.data);
        })

        .catch((e)=>{
          this.dialog_titile = 'ERROR';
          this.dialog_text = 'Faltan Datos a Ingresar';
          console.log(e.response);
        })

      },

      clear () {
        this.atributos.nombre = ''
        this.atributos.municipio = ''
        this.atributos.departamento = ''
        this.atributos.fecha = ''
        this.atributos.hora = ''
        this.atributos.descripcion = ''
        this.$refs.observer.reset()
      },

      inicio()
      {
        this.$router.push('./')
      }
    },
  }
</script>