<template>
    <v-container>
        <h1 class="text-center">Inscribirse Evento</h1>
        <v-spacer></v-spacer>

        <validation-observer
            ref="observer"
            v-slot="{ invalid }"
        >
            <form @submit.prevent="submit">
            <validation-provider
                v-slot="{ errors }"
                name="Nombre Evento"
                rules="required|max:50"
            >
                <v-text-field
                v-model="nombreEvento"
                :counter="50"
                :error-messages="errors"
                label="Nombre Evento"
                required
                ></v-text-field>
            </validation-provider>

            <validation-provider
                v-slot="{ errors }"
                name="Fecha"
                rules="required|max:50"
            >
                <v-text-field
                v-model="Fecha"
                :counter="50"
                :error-messages="errors"
                label="Fecha"
                required
                ></v-text-field>
            </validation-provider>

            <validation-provider
                v-slot="{ errors }"
                name="NombreCiclista"
                rules="required|max:50"
            >
                <v-text-field
                v-model="NombreCiclista"
                :counter="50"
                :error-messages="errors"
                label="Nombre Ciclista"
                required
                ></v-text-field>
            </validation-provider>

            <validation-provider
                v-slot="{ errors }"
                name="Cedula"
                rules="required|max:50"
            >
                <v-text-field
                v-model="Cedula"
                :counter="50"
                :error-messages="errors"
                label="Cedula"
                required
                ></v-text-field>
            </validation-provider>
            
            <validation-provider
                v-slot="{ errors }"
                name="Domicilio"
                rules="required|max:50"
            >
                <v-text-field
                v-model="Domicilio"
                :counter="50"
                :error-messages="errors"
                label="Domicilio"
                required
                ></v-text-field>
            </validation-provider>

            <v-btn
                class="mr-4"
                type="submit"
                :disabled="invalid"
            >
                Confirmar
            </v-btn>
            <v-btn @click="clear"
                class="mr-4">
                Limpiar
            </v-btn>
            <v-btn @click="inicio"
                class="mr-4">
                Cancelar
            </v-btn>
            </form>
        </validation-observer>
    </v-container>
</template>

<script>
  import { required, digits, email, max, regex } from 'vee-validate/dist/rules'
  import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'

  setInteractionMode('eager')

  extend('digits', {
    ...digits,
    message: '{_field_} debe tener minimo {length} digitos. ({_value_})',
  })

  extend('required', {
    ...required,
    message: '{_field_} es obligatorio',
  })

  extend('max', {
    ...max,
    message: '{_field_} no puede tener mas de {length} caracteres',
  })

  extend('regex', {
    ...regex,
    message: '{_field_} {_value_} no son iguales {regex}',
  })

  extend('email', {
    ...email,
    message: 'Email debe tener un formato valido',
  })

  export default {
    components: {
      ValidationProvider,
      ValidationObserver,
    },
    data: () => ({
      nombreEvento: '',
      Fecha: '',
      NombreCiclista: '',
      Cedula: '',
      Domicilio: '',
    }),

    methods: {
      submit () {
        this.$refs.observer.validate()
      },
      clear () {
        this.nombreEvento = ''
        this.Fecha = ''
        this.NombreCiclista = ''
        this.Cedula = ''
        this.Domicilio = ''
        this.$refs.observer.reset()
      },
      inicio()
      {
        this.$router.push('./')
      }
    },
  }
</script>